import uniLoadMore from '@/colorui/components/uni-load-more.vue';
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			isAdd: true,
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBar,
			ScreenHeight: this.ScreenHeight,
			typeIndex: 0,
			types:["请选择", "维修", "保养"],
			isShowCarModal: false,
			cars: [],
			page: 1,
			pageSize: 100,
			status: 'more',
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			oIndex: 0,
			organizations: [{
				org_name: "请选择",
				org_id: ""
			}],
			para: {
				id: "",
				car_id: "",
				car_number: "",
				car_type: "",
				brand: "",
				type: 0,
				desc: "",
				repair_shop_id: "",
				repair_shop: ""
			}
		}
	},
	onLoad(option) {
		if(option.para){
			this.isAdd = false;
			var info = JSON.parse(decodeURIComponent(option.para));
			this.para.id = info.id;
			this.typeIndex = info.type;
			this.para.type = info.type;
			this.para.car_id = info.car_id;
			this.para.car_number = info.car_number;
			this.para.car_type = info.car_type;
			this.para.brand = info.brand;
			this.para.desc = info.desc;
			this.para.repair_shop_id = info.repair_shop_id;
			this.para.repair_shop = info.repair_shop;
		}
		//获取维修厂数据
		global.$http.post('/core/organization/getRepairShopByOrg', {
			params: {},
		}).then(res => {
			if (res.status === "0") {
				for (var i = 0; i < res.data.length; i++) {
					this.organizations.push(res.data[i]);
					if(option.para){
						if(res.data[i].org_id == info.repair_shop_id)
						{
							this.oIndex = i + 1;
						}
					}
				}
			} else {
				uni.showToast({
					title: res.msg,
					icon: 'none'
				});
			}
		}).catch(err => {
			uni.hideLoading();
			uni.showToast({
				title: err.message,
				icon: 'none'
			});
		});
	},
	methods: {
		getCarListData() {
			this.status = 'loading';
			global.$http.post('/car/info/getCarByDriver', {
				params: {
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					this.status = 'noMore';
					this.cars = res.data;
				}else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		ChangeTypes: function(e){
			this.typeIndex = e.detail.value;
			this.para.type = e.detail.value;
		},
		showCarModal: function(e) {
			this.page = 1;
			this.cars = [];
			this.isShowCarModal = true;
			this.getCarListData();
		},
		hideCarModal: function(e){
			this.isShowCarModal = false;
		},
		getCar: function(e) {
			this.para.car_id = e.id;
			this.para.car_number = e.car_number;
			this.para.brand = e.brand;
			this.para.car_type = e.car_type;
			this.isShowCarModal = false;
		},
		OrganizationChange: function(e) {
			this.para.repair_shop_id = this.organizations[e.detail.value].org_id;
			this.para.repair_shop = this.organizations[e.detail.value].org_name;
			if (this.oIndex != e.detail.value) {
				this.oIndex = e.detail.value;
			}
		},
		Submit: function(e){
			//验证数据
			if(this.para.desc.length <= 0){
				uni.showToast({
					icon: 'none',
					title: '请填写维修原由'
				});
				return;
			}
			if(this.para.type <= 0){
				uni.showToast({
					icon: 'none',
					title: '请选择维修类型'
				});
				return;
			}
			if(this.para.car_id <= 0){
				uni.showToast({
					icon: 'none',
					title: '请选择维修车辆'
				});
				return;
			}
			if(this.para.repair_shop_id.length <= 0){
				uni.showToast({
					icon: 'none',
					title: '请选择维修厂'
				});
				return;
			}
			//提交数据
			uni.showLoading({
				title: '提交中',
				mask: false
			});
			global.$http.post('/car/repair/' + (this.isAdd ? 'create' : 'update'), {
				params: this.para,
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					uni.showToast({
						icon: 'none',
						title: '提交成功'
					});
					uni.navigateBack();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		}
	}
}
